.section__doing {
  @media screen and (max-width: 1199px) {
    display: none;
  }
}

.doing {
  display: flex;
  align-items: center;
}

.doing__item {
  position: relative;
  &:not(:last-child) {
    margin-right: 30px;
  }
}

.doing__img {
  display: block;
  max-width: 100%;
  height: auto;
}

.doing__description {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: rgba(47, 48, 58, 0.8);
}

.doing__description-text {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: $color-head;
}
